<template>

  <div class="" style="background:#fff;overflow-y:hidden;display: flex;flex-grow: 1;">

      <button v-on:click="openRSocket()">Open RSocket</button>
      <button v-on:click="sendMessageOverRSocket()">Send Msg over RSocket</button>

      <button v-on:click="sendStreamConnectOverRSocket()">Connect to RSocket stream</button>
      <button v-on:click="requestMoreOnSubscribedStream()">Request more on RSocket stream</button>
      <button v-on:click="cancelStreamSubscription()">Cancel RSocket stream subscription</button>


      <button v-on:click="connectToChannel()">Connect to RSocket channel</button>
      <button v-on:click="sendBroadcastMessageOverRSocket()">Broadcast RSocket</button>



  </div>

</template>

<script>

  import {
    RSocketClient,
    JsonSerializer,
    IdentitySerializer,
    JsonSerializers,
    BufferEncoders,
    encodeAndAddCustomMetadata,
    encodeAndAddWellKnownMetadata,
    TEXT_PLAIN,
    MESSAGE_RSOCKET_COMPOSITE_METADATA,
    MESSAGE_RSOCKET_ROUTING,
  } from 'rsocket-core';
  import RSocketWebSocketClient from "rsocket-websocket-client";
  import {Flowable, Single} from 'rsocket-flowable';
  import RSocketResponder from './rsocketResponder.js';

  export default {
    mixins :    [],
    data: function() {
      return {
        socket : null,
        streamSubscription : null
      }
    },
    methods: {

      openRSocket(){

        console.log("connecting with RSocket @9001...");
        console.log("RSocketResponder : ", RSocketResponder);

            const transport = new RSocketWebSocketClient({
                url: "ws://localhost:9000/rsocket"
            });

            const client = new RSocketClient({
                // send/receive JSON objects instead of strings/buffers
                serializers: {
                  data: JsonSerializer,
                  metadata: IdentitySerializer
                },
                setup: {
                  dataMimeType: 'application/json',
                  keepAlive: 3600000,     // 3600000 = 8hrs  // When this time elapses without any data, then server throws this exception :: io.rsocket.exceptions.ConnectionErrorException: No keep-alive acks for 100000 ms
                  lifetime: 3600000,
                  metadataMimeType: 'message/x.rsocket.routing.v0',
                },
                //serializers: JsonSerializers,
                // setup: {
                //   route : "request-response",
                //   data : {"origin" : "Client", "interaction" : "Request"},
                //   // ms btw sending keepalive to server
                //   keepAlive: 60000,
                //   // ms timeout if no keepalive response
                //   lifetime: 180000,
                //   // format of `data`
                //   dataMimeType: "application/json",
                //   // format of `metadata`
                //   metadataMimeType: "application/json"
                // },
                transport : transport,
                //responder : new RSocketResponder()      //Both class and the below code works for capturing server pushed data.
                responder : {
                  fireAndForget : (payload) => {

                      console.log("Message pushed from server, ", payload);

                      // if(payload.metadata.get(Metadata.ROUTE) == "send.to.location") {
                      //     const radar = payload.data;
                      //     this.map.panTo([radar.location.lat, radar.location.lng]);
                      // }
                  }
                }
            });



            client.connect().subscribe({
                onComplete: socket => {
                  this.socket = socket;
                  console.log("RSocket connected.");
                },
                onError: error => {
                  console.log("got connection error");
                  console.error(error);
                },
                onSubscribe: cancel => {
                  // call cancel() to abort
                }
            });


      },

      sendMessageOverRSocket(){

          let socket = this.socket;
          let route = "request-response";

          let requestResponseData = {
               metadata : String.fromCharCode(route.length) + route,
               data : {"origin" : "Client", "interaction" : "Request"},
//              metadataMimeType: 'text/plain',
//               dataMimeType : 'application/octet-stream',
               // metadata : {
               //   //metadataMimeType: 'text/plain',
               //   metadataMimeType : MESSAGE_RSOCKET_COMPOSITE_METADATA.string,
               //   dataMimeType : 'application/octet-stream',
               //   route : "request-response"
               // },

               //metadata :{},

              //route : "request-response",
              //data: new Buffer("{\"origin\" : \"Client\", \"interaction\" : \"Request\"}"),
              // metadata: encodeAndAddWellKnownMetadata(
              //   encodeAndAddCustomMetadata(
              //     Buffer.alloc(0),
              //     bearerMimeType,
              //     Buffer.from(jwtToken)
              //   ),
              //   MESSAGE_RSOCKET_ROUTING,
              //   Buffer.from(String.fromCharCode(route.length) + route)
              // )
          };

          let handleRSocketResponse = (response) => {
            console.log("RSocket's response : ", response);
          };

          socket.requestResponse(requestResponseData)
                .subscribe({
                  onComplete : response => handleRSocketResponse(response),
                  onError    : error    => console.error(error)
                });

      },

      sendStreamConnectOverRSocket(){

          let socket = this.socket;
          let route = "connect-to-stream";

          let requestResponseData = {
               metadata : String.fromCharCode(route.length) + route,
               data : {"origin" : "Client", "interaction" : "Request"},
          };

          let handleRSocketResponse = (response) => {
            console.log("RSocket's stream sent complete response (No more available data.): ", response);
          };

          console.log("Requesting to connect to stream..");

          let subscription = null;

          socket.requestStream(requestResponseData)
                .subscribe({
                  onSubscribe: sub => {
                    this.streamSubscription = sub;
                    console.log(`Client is establishing a channel`);
                    //subscription.request(0x7fffffff);
                    this.streamSubscription.request(1);
                  },
                  onComplete : response => handleRSocketResponse(response),   // This will be called when the data upstream is all sent.
                  onError    : error    => console.error(error),              //
              		onNext: value => {                                          // To process the individual item of the upstream as they are sent.
              		  console.log("got next value in requestStream..", value);
              		  //this.received.push(value.data);
              		},
                });
          console.log("Requesting to connect to stream done and waiting for its response..");
      },
      cancelStreamSubscription(){
        this.streamSubscription.cancel(); //There won't be any callback for this operation.
      },
      requestMoreOnSubscribedStream(){
        this.streamSubscription.request(3);
        console.log("Requested 3 more data..");
      },

      sendBroadcastMessageOverRSocket(){

          let socket = this.socket;
          let route = "broadcast";

          let requestResponseData = {
               metadata : String.fromCharCode(route.length) + route,
               data : {"origin" : "Client", "interaction" : "Request"},
          };

          socket.fireAndForget(requestResponseData);
      },
      connectToChannel(){

          let socket = this.socket;
          let route = "connect-to-channel";

          let requestResponseData = {
               metadata : String.fromCharCode(route.length) + route,
               data : {"origin" : "Client", "interaction" : "Request"},
          };

          let handleRSocketResponse = (response) => {
            console.log("RSocket's broadcast response : ", response);
          };

          //socket.fireAndForget(requestResponseData);

          const message = { message: "requestStream from JavaScript!" };

          console.log("Flowable : ", Flowable);
          console.log("RSocketClient : ", RSocketClient);
          const flowablePayload = new Flowable(subscriber => {
              	  subscriber.onSubscribe({
                  		cancel: () => {},
                  		request: n => {
                  		  for (let index = 0; index < n; index++) {
                    			const message = {
                    			  message: "requestChannel from JavaScript! #" + index
                    			};
                    			subscriber.onNext(message);
                  		  }
                  		  subscriber.onComplete();
                  		}
              	  });
          	  });

        	this.socket
        	  .requestChannel({
            		data: flowablePayload,
                //data : Flowable.just([{data:{"origin" : "Client", "interaction" : "Request 1"}},
                //                      {data:{"origin" : "Client", "interaction" : "Request 2"}}]),
                metadata : String.fromCharCode(route.length) + route,
             })
        	  .subscribe({
            		onComplete: resp => {
            		  console.log("requestStream done", resp);
            		  //this.received.push("requestStream done");
            		},
            		onError: error => {
            		  console.log("got error with requestStream");
            		  console.error(error);
            		},
            		onNext: value => {
            		  console.log("got next value in requestStream..", value);
            		  //this.received.push(value.data);
            		},
            		// Nothing happens until `request(n)` is called
            		onSubscribe: sub => {
            		  console.log("subscribe request Stream!");
            		  sub.request(1);
            		  //this.sent.push(message);
            		}
        	  });


      },

    },
    created: function() {},
    computed: {
    },
    mounted: function() {
      console.log("RSocket lab component mounted. ");
    },
    watch: {

    }
  }

</script>

